.navbar {
  background-color: black;
  height: 60px;
  position: sticky;
  top: 0;
  width: 100%;
  overflow: hidden;
}

 .navbu {
  float: right;
  padding: 5px;
  text-decoration: none;
  font-weight: bold;
  color: white;
}

.app-container{
  text-align: center;
}

.form{
  position: fixed;
  left: 45%;
  top: 40%;
  display: inline-block;
  border-style: solid;
  border-color: black;
  border-width: medium;
  width:140px;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
